.message {
  position: relative;
  display: flex;
  gap: 20px;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 5px;
  position: relative;
  padding: 10px 20px 10px 20px;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.25);
}

.content:before, .content:after {
	content: "";
  z-index: -1;
  position: absolute;
  top: 20px;
  margin-top:-5px;
  left: -5px;
  width: 10px;
  height: 10px;
  background: #ffffff;
  border-width: 0 0 1px 1px;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.25);
  transform: rotate(45deg);
}

.content:after {
  z-index: 1;
  box-shadow: none;
}

.name {
  color: #66cc66;
  font-variation-settings: "wght" 500;
}

.text {
  margin: 0;
}